<template>
    <el-dialog class="ui-layout_edit-dialog" custom-class="ui-com_edit" width="1200px" top="15vh" title="查看图片"
               :visible="show" @close="handleClose">
        <div class="ui-look_image--list">
            <ul class="clearFix">
                <li v-for="(item, index) in imgs" :style="{background:'url('+item+') no-repeat center/contain'}"
                    @click="handleLookImage(imgs, index)"></li>
            </ul>
        </div>
        <LookImage :show.sync="isShow" :src="showUrl"></LookImage>
    </el-dialog>
</template>

<script>
export default {
    name: 'PreviewComImage',
    props: {
        show: {
            type: Boolean,
            default: false
        },
        imgs: {
            type: Array,
            default () {
                return [];
            }
        }
    },
    data () {
        return {
            isShow: false,
            showUrl: ''
        };
    },
    methods: {
        handleClose () {
            this.$emit('update:show', false);
        },
        handleLookImage (urls, index) {
            this.isShow = true;
            this.showUrl = [...urls, index];
        }
    }
};
</script>

<style scoped lang="less">
.ui-look_image--list {
    ul li {
        width: 130px;
        height: 130px;
        float: left;
        margin-right: 16px;
        margin-bottom: 16px;
        border: 1px solid #e6e6e6;

        &:nth-child(8n) {
            margin-right: 0;
        }
    }
}
</style>



