<template>
    <div>
        <div @click="handleClick" class="ui-table__img">
            <div class="ui-image--static" v-show="src && !isComplete && !errorSrc" :style="{width, height}"></div>
            <img v-show="false" :width="width" :height="height" :src="src" alt="" ref="myImg">
            <div v-if="isComplete && src" :style="{width, height, background: `url(${src}) no-repeat center/contain`}"></div>
            <div v-if="errorSrc || !src" :style="{width, height, lineHeight: height}">
                <img style="width: 100%;" src="../assets/image/default-img.png">
            </div>
        </div>
        <PreviewComImage :imgs="previewListImage" :show.sync="isShowListImg"></PreviewComImage>
        <PreviewImage :show.sync="isShowArtImg" :row="previewArtInfo"></PreviewImage>
    </div>
</template>

<script>
import PreviewImage from './PreviewImage';

import PreviewComImage from './PreviewComImage';

import {
    previewImageDetailInfo,
} from '@/api/design_department/artist_manage/art_history_img';

export default {
    name: 'LoadingImg',
    components: {
        PreviewComImage,
        PreviewImage
    },
    props: {
        src: {
            type: String,
            default: ''
        },
        width: {
            type: String,
            default: '60px'
        },
        height: {
            type: String,
            default: '60px'
        },
        row: {
            type: Object,
            default: null
        }
    },
    data () {
        return {
            isComplete: false,
            errorSrc: false,

            isShowArtImg: false,
            previewArtInfo: {},

            isShowListImg: false,
            previewListImage: []
        };
    },
    methods: {
        handleClick (e) {
            if (!this.isComplete) return;
            if (this.row) {
                if (this.row.is_art_image == '1' && this.row.art_type) {
                    let spu = this.row.spu[0];
                    if (this.row.art_type.split(',').length > 1) {
                        let types = this.row.art_type.split(',');
                        let listInfo1 = [];
                        previewImageDetailInfo({
                            image_type: types[0],
                            spu: spu
                        }).then(({data}) => {
                            listInfo1 = data.data.list;
                            previewImageDetailInfo({
                                image_type: types[1],
                                spu: spu
                            }).then(({data}) => {
                                let listInfo2 = data.data.list;
                                if (!Array.isArray(listInfo1)) {
                                    for (let [key, value] of Object.entries(listInfo1.spu)) {
                                        let newVal = {
                                            '1': value
                                        };
                                        if (!Array.isArray(listInfo2)) {
                                            newVal['2'] = data.data.list.spu[key];
                                        }
                                        listInfo1.spu[key] = !Array.isArray(listInfo2) ? newVal : value;
                                    }
                                    for (let [key, value] of Object.entries(listInfo1.sku)) {
                                        let newVal = {
                                            '1': value
                                        };
                                        if (!Array.isArray(listInfo2)) {
                                            if (listInfo2.sku[key]) {
                                                newVal['2'] = listInfo2.sku[key];
                                            } else {
                                                newVal['2'] = {
                                                    main_pics: [],
                                                    assiant_pics: []
                                                };
                                            }
                                        }
                                        listInfo1.sku[key] = !Array.isArray(listInfo2) ? newVal : value;
                                    }
                                } else if (!Array.isArray(listInfo2)) {
                                    for (let [key, value] of Object.entries(listInfo2.spu)) {
                                        let newVal = {
                                            '2': value
                                        };
                                        listInfo1.spu[key] = newVal;
                                    }
                                    for (let [key, value] of Object.entries(listInfo2.sku)) {
                                        let newVal = {
                                            '2': value
                                        };
                                        listInfo1.sku[key] = newVal;
                                    }
                                }
                                this.isShowArtImg = true;
                                this.previewArtInfo = {
                                    checks: !Array.isArray(listInfo1) && !Array.isArray(listInfo2) && 1,
                                    ...listInfo1,
                                    imgTypeList: {
                                        '1': '实拍图',
                                        '2': '实拍无logo',
                                        '3': '试卖',
                                        '4': '代销'
                                    },
                                    imgType: !Array.isArray(listInfo1) ? '1' : '2',
                                };
                            });
                        });
                    } else {
                        previewImageDetailInfo({
                            image_type: this.row.art_type,
                            spu: spu
                        }).then(({data}) => {
                            this.isShowArtImg = true;
                            this.previewArtInfo = {
                                ...data.data.list,
                                imgTypeList: {
                                    '1': '实拍图',
                                    '2': '实拍无logo',
                                    '3': '试卖',
                                    '4': '代销'
                                },
                                imgType: row.art_type
                            };
                        });
                    }
                } else if (Array.isArray(this.row.is_art_image)) {
                    this.isShowListImg = true;
                    this.previewListImage = this.row.is_art_image;
                }
            } else {
                this.$emit('click', e);
            }
        }
    },
    mounted () {
        const vm = this;
        this.$nextTick(() => {
            if (this.src) {
                this.$refs.myImg.addEventListener('load', function () {
                    vm.isComplete = true;
                });

                this.$refs.myImg.addEventListener('error', function () {
                    vm.errorSrc = true;
                });
            }
        });
    }
};
</script>

<style scoped>
.ui-table__img {
    /*background: #eeeeee;*/
    border: 1px solid #e6e6e6;
    display: inline-block;
    text-align: center;
}

.ui-table__img img {
    width: 100%;
    height: 100%;
}

.ui-image--static {
    padding: 10px;
    box-sizing: border-box;
    background: url("../assets/image/img_loading.gif") no-repeat center/50%;
}

.icon-tupianshangchuan {
    font-size: 34px;
    vertical-align: middle;
    margin: 0;
}
</style>



